-decoration: none;\n  outline: none;\n  color: $white-color;\n}\n\nul,\nol {\n  padding: 0;\n  margin: 0;\n}\n\n/*---------------------\n  Helper CSS\n-----------------------*/\n.section-title {\n  margin-bottom: 30px;\n\n  h4,\n  h5 {\n    color: $white-color;\n    font-weight: 600;\n    line-height: 21px;\n    text-transform: uppercase;\n    padding-left: 20px;\n    position: relative;\n    font-family: 'Oswald', sans-serif;\n\n    &:after {\n      position: absolute;\n      left: 0;\n      top: -6px;\n      height: 32px;\n      width: 4px;\n      background: $primary-color;\n      content: \"\";\n    }\n  }\n}\n\n.set-bg {\n  background-repeat: no-repeat;\n  background-size: cover;\n  background-position: top center;\n}\n\n.spad {\n  padding-top: 100px;\n  padding-bottom: 100px;\n}\n\n.text-white h1,\n.text-white h2,\n.text-white h3,\n.text-white h4,\n.text-white h5,\n.text-white h6,\n.text-white p,\n.text-white span,\n.text-white li,\n.text-white a {\n  color: #fff;\n}\n\n/* buttons */\n.primary-btn {\n  display: inline-block;\n  font-size: 13px;\n  font-weight: 700;\n  text-transform: uppercase;\n  color: $white-color;\n  letter-spacing: 2px;\n\n  span {\n    font-size: 18px;\n    margin-left: 5px;\n    position: relative;\n    top: 3px;\n  }\n}\n\n.site-btn {\n  font-size: 13px;\n  color: $white-color;\n  background: $primary-color;\n  font-weight: 700;\n  border: none;\n  border-radius: 2px;\n  letter-spacing: 2px;\n  text-transform: uppercase;\n  display: inline-block;\n  padding: 12px 30px;\n}\n\n/* Preloder */\n.preloder {\n  position: fixed;\n  width: 100%;\n  height: 100%;\n  top: 0;\n  left: 0;\n  z-index: 999999;\n  background: #000;\n  visibility: hidden;\n  opacity: 0;\n  transition: .2s;\n}\n\n.preloder.active {\n  visibility: visible;\n  opacity: 1;\n}\n\n.loader {\n  width: 40px;\n  height: 40px;\n  position: absolute;\n  top: 50%;\n  left: 50%;\n  margin-top: -13px;\n  margin-left: -13px;\n  border-radius: 60px;\n  animation: loader 0.8s linear i